.container {
    display: flex;
}

.sidebar {
    width: 100px; /* Set the width of your sidebar */
    background-color: #333; /* Set the background color */
    color: #fff; /* Set text color */
    position: fixed; /* Make it fixed */
    height: 100%; /* Make it take up the full height of the viewport */
    justify-content: space-between;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.content {
    flex-grow: 1; /* Allow the main content to grow and fill remaining space */
    overflow: auto; /* Add this line */
    padding: 20px; /* Add some padding to avoid overlapping with the sidebar */
    margin-left: 100px;
}

.sidebar-navigation {
margin: 10px;
margin-top: 50px;


}
.navlink{
  margin: 10px;
  padding: 10px;
  border: 2px solid #111;
  display: flex;
  justify-content: center;
}

.sidebar-top{
  display: flex;
  justify-content: center;
  padding-top: 10px;
}